<template>
  <div id="parameterSettings">
    <div>
      <Form ref="parameter" :rules="ruleValidate" :label-width="145" >
        <FormItem prop="redLineUrl" label="临时文件夹：">
          <Input v-model.trim="redLineUrl" placeholder="请输入临时文件夹路径" size="default" >
            <Button slot="append" @click="save(redLineUrl)" type="primary">保存</Button>
          </Input>
        </FormItem>
        <FormItem prop="redLineUrl" label="主题定制：" style="text-align: left">
          <Col span="4" style="text-align: center">
            <span :style="{background: color2 === '' ? '#fff' : color2}" style="display: inline-block;width: 30px;height: 30px;border: 1px solid #d1d1d1;border-radius: 4px"></span>
          </Col>
          <Col span="12">
            <ColorPicker v-model="color2" />
          </Col>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
import {
  // 获取服务地址
  lswjj
} from '@/api/systemManage'
export default {
  name: 'parameterSettings',
  data () {
    return {
      /* 数据表单参数 */
      redLineUrl: '',
      /* 数据表单校验 */
      ruleValidate: {},
      color2: ''
    }
  },
  components: {},
  computed: {
    theme () {
      return this.$store.state.theme
    }
  },
  mounted () {
    this.save()
  },
  watch: {
    color2 (val) {
      this.$store.commit('setTheme', val)
      sessionStorage.setItem('theme', val)
    },
    theme (val) {
      this.color2 = val
    }
  },
  methods: {
    save (url) {
      lswjj({
        data: {
          data: url
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.redLineUrl = res.data.data
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  #parameterSettings {
    &>div {
      height: 100%;
      overflow: auto;
    }
    .ivu-form {
      width: 50%;
      margin: auto;
      padding: 4% 0;
    }
    .btn-cancel {
      border: 1px solid #d7dde4;
      background: #d7dde4;
      margin-left: 20px;
      &:hover {
        color: #57a3f3;
        background-color: #fff !important;
        border-color: #57a3f3 !important;
      }
    }
    .mt50 {
      margin-top: 5%;
    }
  }
</style>
